
.lazy-wrap[data-v-2] {
    display: block;
    box-sizing: border-box;
    width: 83.3vmin;
    height: 100vmin;
    margin: 0 auto;
    border: 1px solid #e1e1e1;
    border-radius: 4px;
    overflow: hidden;
}
.img-wrap[data-v-2] {
    display: block;
    box-sizing: border-box;
    width: 83.3vmin;
    height: 83.3vmin;
    margin: 0 auto;
}
.img-wrap img[data-v-2] {
    width: 100%;
}
.lazy-content .desc[data-v-2] {
    color: #333;
    font-size: 14px;
    box-sizing: border-box;
    height: 16.7vmin;
    line-height: 1.5;
    padding: 10px 5px;
}
.desc p[data-v-2] {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}



