
body {
    font-family: "PingFang SC", "Hiragino Sans GB", "Helvetica Neue", Roboto, Noto, sans-serif;
    font-size: 12px;
    -webkit-font-smoothing: antialiased;
    text-justify: 100%;
    margin: 8px;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}



